import React from 'react';
import { main } from '../wailsjs/go/models';

interface TaskStatsProps {
  stats: main.StatsDTO | null;
  filter: 'all' | 'active' | 'completed';
  onFilterChange: (filter: 'all' | 'active' | 'completed') => void;
  onClearCompleted: () => void;
  loading?: boolean;
}

export const TaskStats: React.FC<TaskStatsProps> = ({
  stats,
  filter,
  onFilterChange,
  onClearCompleted,
  loading = false,
}) => {
  if (!stats) return null;

  const activeCount = stats.active || 0;
  const completedCount = stats.completed || 0;
  const totalCount = stats.total || 0;

  const getFilterClass = (currentFilter: string) =>
    `px-3 py-1 text-sm font-medium rounded-full transition-colors ${
      filter === currentFilter
        ? 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-100'
        : 'text-gray-600 hover:bg-gray-100 dark:text-gray-300 dark:hover:bg-gray-700'
    }`;

  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-4">
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center space-y-4 sm:space-y-0">
        <div className="text-sm text-gray-500 dark:text-gray-400">
          {activeCount} {activeCount === 1 ? 'task' : 'tasks'} left
        </div>

        <div className="flex items-center space-x-1">
          <button
            type="button"
            onClick={() => onFilterChange('all')}
            className={getFilterClass('all')}
            disabled={loading}
          >
            All
          </button>
          <button
            type="button"
            onClick={() => onFilterChange('active')}
            className={getFilterClass('active')}
            disabled={loading}
          >
            Active
          </button>
          <button
            type="button"
            onClick={() => onFilterChange('completed')}
            className={getFilterClass('completed')}
            disabled={loading}
          >
            Completed
          </button>
        </div>

        {completedCount > 0 && (
          <button
            type="button"
            onClick={onClearCompleted}
            disabled={loading}
            className="text-sm text-red-600 hover:text-red-800 dark:text-red-400 dark:hover:text-red-300 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
          >
            Clear completed
          </button>
        )}
      </div>

      {/* Progress bar */}
      {totalCount > 0 && (
        <div className="mt-4">
          <div className="h-2 bg-gray-200 dark:bg-gray-700 rounded-full overflow-hidden">
            <div
              className="h-full bg-green-500 transition-all duration-300"
              style={{
                width: `${totalCount > 0 ? (completedCount / totalCount) * 100 : 0}%`,
              }}
            />
          </div>
          <div className="mt-1 text-xs text-gray-500 dark:text-gray-400 text-right">
            {completedCount} of {totalCount} tasks completed (
            {totalCount > 0 ? Math.round((completedCount / totalCount) * 100) : 0}%)
          </div>
        </div>
      )}
    </div>
  );
};

export default TaskStats;
