import React, { useState, useCallback } from 'react';
import { useTasks } from './hooks/useTasks';
import { ThemeProvider, useTheme } from './contexts/ThemeContext';
import { Priority, Filter, TaskDTO } from './types';
import { ErrorBoundary } from './components/ErrorBoundary';
import TaskList from './components/TaskList';
import { TaskForm } from './components/TaskForm';
import { TaskStats } from './components/TaskStats';

// Main App component wrapped with ThemeProvider
const App: React.FC = () => {
  return (
    <ErrorBoundary 
      fallback={
        <div className="min-h-screen flex items-center justify-center p-4">
          <div className="max-w-md w-full p-6 bg-white dark:bg-gray-800 rounded-lg shadow-lg text-center">
            <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
              Oops! Something went wrong
            </h2>
            <p className="text-gray-600 dark:text-gray-300 mb-4">
              The application has encountered an error. Please try refreshing the page.
            </p>
            <button
              onClick={() => window.location.reload()}
              className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 dark:focus:ring-offset-gray-800"
            >
              Refresh Page
            </button>
          </div>
        </div>
      }
    >
      <ThemeProvider>
        <AppContent />
      </ThemeProvider>
    </ErrorBoundary>
  );
};

// AppContent component that uses the theme
const AppContent: React.FC = () => {
  const { theme, toggleTheme, isDark } = useTheme();
  const [filter, setFilter] = useState<Filter>('all');
  const [editingTask, setEditingTask] = useState<TaskDTO | null>(null);
  
  const {
    tasks,
    stats,
    loading,
    error,
    addTask,
    toggleTask,
    removeTask,
    clearCompletedTasks,
    editTask,
    refetch
  } = useTasks();

  const handleAddTask = useCallback(async (data: { title: string; priority: Priority; dueDate: Date | string | null }) => {
    try {
      await addTask({
        title: data.title,
        priority: data.priority,
        dueDate: data.dueDate || null
      });
    } catch (err) {
      console.error('Failed to add task:', err);
      alert('Failed to add task. Please try again.');
    }
  }, [addTask]);

  const handleToggleTask = useCallback(async (id: number) => {
    try {
      await toggleTask(id);
    } catch (err) {
      console.error('Failed to toggle task:', err);
      alert('Failed to update task status. Please try again.');
    }
  }, [toggleTask]);

  const handleDeleteTask = useCallback(async (id: number) => {
    try {
      await removeTask(id);
    } catch (err) {
      console.error('Failed to delete task:', err);
      alert('Failed to delete task. Please try again.');
    }
  }, [removeTask]);

  const handleEditTask = useCallback((task: TaskDTO) => {
    setEditingTask(task);
  }, []);

  const handleUpdateTask = useCallback(async (data: { title: string; priority: Priority; dueDate: Date | string | null }) => {
    if (!editingTask) return;
    
    try {
      await editTask(editingTask.id, {
        title: data.title,
        priority: data.priority,
        dueDate: data.dueDate || null
      });
      setEditingTask(null);
    } catch (err) {
      console.error('Failed to update task:', err);
      alert('Failed to update task. Please try again.');
    }
  }, [editTask, editingTask]);

  const handleClearCompleted = useCallback(async () => {
    try {
      await clearCompletedTasks();
    } catch (err) {
      console.error('Failed to clear completed tasks:', err);
      alert('Failed to clear completed tasks. Please try again.');
    }
  }, [clearCompletedTasks]);

  const handleFilterChange = useCallback((newFilter: Filter) => {
    setFilter(newFilter);
  }, []);

  // Error boundary fallback UI
  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center p-4">
        <div className="max-w-md w-full p-6 bg-white dark:bg-gray-800 rounded-lg shadow-lg text-center">
          <div className="text-red-500 text-5xl mb-4"></div>
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">Something went wrong</h2>
          <p className="text-gray-600 dark:text-gray-300 mb-6">
            {error.message || 'An unexpected error occurred. Please try again later.'}
          </p>
          <button
            onClick={() => window.location.reload()}
            className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
          >
            Reload Application
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className={`min-h-screen transition-colors duration-200 ${isDark ? 'dark bg-gray-900' : 'bg-gray-50'}`}>
      <div className="max-w-4xl mx-auto p-4 md:p-6">
        {/* Header */}
        <header className="flex items-center justify-between mb-8">
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white">Task Manager</h1>
          <button
            onClick={toggleTheme}
            className="p-2 rounded-full text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500"
            aria-label={isDark ? 'Switch to light mode' : 'Switch to dark mode'}
          >
            {isDark ? (
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
              </svg>
            ) : (
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
              </svg>
            )}
          </button>
        </header>

        {/* Add Task Form */}
        <div className="mb-8">
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">
              {editingTask ? 'Edit Task' : 'Add New Task'}
            </h2>
            <TaskForm
              initialData={editingTask || undefined}
              onSubmit={editingTask ? handleUpdateTask : handleAddTask}
              loading={loading}
              submitText={editingTask ? 'Update Task' : 'Add Task'}
            />
          </div>
        </div>

        {/* Task Stats and Filters */}
        <div className="mb-6">
          <TaskStats
            stats={stats}
            filter={filter}
            onFilterChange={handleFilterChange}
            onClearCompleted={handleClearCompleted}
            loading={loading}
          />
        </div>

        {/* Task List */}
        <div className="mb-8">
          <TaskList
            tasks={tasks}
            filter={filter}
            onToggleTask={handleToggleTask}
            onEditTask={handleEditTask}
            onDeleteTask={handleDeleteTask}
            loading={loading}
            emptyState={
              <div className="text-center py-12">
                <div className="text-gray-500 dark:text-gray-400">
                  <svg
                    className="mx-auto h-12 w-12 text-gray-400"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={1}
                      d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2"
                    />
                  </svg>
                  <h3 className="mt-2 text-sm font-medium text-gray-900 dark:text-white">
                    No tasks yet
                  </h3>
                  <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
                    Get started by creating a new task above.
                  </p>
                </div>
              </div>
            }
          />
        </div>

        {/* Footer */}
        <footer className="text-center text-sm text-gray-500 dark:text-gray-400">
          <p>Task Manager &copy; {new Date().getFullYear()}</p>
          <p className="text-xs mt-1">Built with Wails, React, and TypeScript</p>
        </footer>
      </div>
    </div>
  );
};

export default App;
